/* body  */

body {
  display: flex;
  justify-content: center;
  height: 100vh;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

/* header */

nav {
  width: 100%;
  position: relative;
  list-style: none;
  font-size: 18px;
  margin: 0;
  /* font-family: ProximaNova-Regular; */
  font-weight: bolder;
}

/* main */

main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 758px;
  width: 1024px;
  background: #f4f4f4;
}

.testing {
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.comment {
  height: 659px;
  width: 430px;
  margin-right: 12.5px;
}

input[type="text"],
select,
input[type="file"] {
  border: none;
  background: #fafafa;
  /* font-family: Proxima Nova; */
  font-weight: lighter;
  font-style: italic;
  text-align: top left;
  width: 380px;
  height: 40px;
  color: #c5c5c5;
  margin: 0;
}

input,
select,
textarea {
  padding-left: 18px;
}

.title-category {
  width: 380px;
  height: 62px;
  padding-bottom: 24px;
}
.title-category p {
  font-size: 12px;
  line-height: 15px;
  height: 15px;
  width: 67px;
  margin: 0;
}

/* new comment/post */

#naming {
  position: relative;
  padding: 5px 0 7px;
}

.newc__sect {
  height: 611px;
  width: 380px;
}

.newc__sect form {
  display: flex;
  flex-direction: column;
}

.newc__sect h1 {
  /* font-family: "proxima Nova"; */
  margin-top: 0;
}

.newc__sect p {
  font-size: 12px;
  color: #404040;
}

.newc__sect textarea {
  height: 214px;
  border: none;
  background: #f4f4f4;
  color: #c5c5c5;
}

#files {
  opacity: 0;
  position: absolute;
  z-index: -1;
}

.submit {
  width: 100%;
  height: 31px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 24px;
}

input[type="submit"] {
  background: #f27623;
  transition: 0.3s;
  width: 192px;
  height: 31px;
  border-radius: 50px;
  border: none;
}
input[type="submit"]:hover {
  cursor: pointer;
  background: #fa3f01;
}

/* bog preview section  */
.prev {
  height: 659px;
  width: 473px;
  margin-left: 12.5px;
  background: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.blog__sect {
  background: white;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.1) 1px 1px 9px 1px;
}

.card-content {
  width: 168px;
  height: 151px;
  overflow: hidden;
}

.card-content p {
  color: #868686;
}

.load-more button {
  width: 192px;
  height: 31px;
  border-radius: 50px;
  border: none;
  background: #f27623;
}
/* footer styling  */

footer {
  position: relative;
  bottom: 0%;
  height: 31px;
  width: 1024px;
  background: #262626;
  display: flex;
  justify-content: center;
  align-items: center;
}

footer p {
  color: white;
}
